import React, { useState, useEffect } from 'react';
import {
  Form, Input, Select, Row, Col, Checkbox, Button, Divider, InputNumber, message
} from 'antd';
import { getHotelSetting, updateHotelSetting } from '../../api/HotelApi';

const { Option } = Select;
const CheckboxGroup = Checkbox.Group;

function HotelSettingForm(props) {
  const [setting, setSetting] = useState({});
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (props.hotelId) {
      fetchHotelSetting(props.hotelId);
    }
  }, [props.hotelId])

  const fetchHotelSetting = async (hotelId) => {
    getHotelSetting({hotelId}).then(res => {
      if (res.status === 'SUCCESS' && res.value) {
        setSetting(res.value);
      }
    })
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!props.hotelId) return;
    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        const { notifyToEmail, notifyCCEmail, notifyBCCEmail, usedCurrency, valueAddedTaxPercent,
          serviceTaxPercent, includeTaxesInPrice, babyAgeDetermine, childAgeDetermine
        } = values;
        const data = {
          hotelId: props.hotelId,
          notifyToEmail, notifyCCEmail, notifyBCCEmail, usedCurrency, valueAddedTaxPercent,
          serviceTaxPercent, includeTaxesInPrice, babyAgeDetermine, childAgeDetermine
        };

        setLoading(true);
        updateHotelSetting(data).then(res => {
          setLoading(false);
          if (res.status === 'SUCCESS') {
            message.success('Cập nhật cấu hình khách sạn thành công');
          } else {
            message.error(res.value || 'Cập nhật cấu hình khách sạn không thành công');
          }
        }).catch(err => {
          setLoading(false);
          message.error(err.message);
        })
      }
    });
  }

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };
  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0,
      },
      sm: {
        span: 16,
        offset: 8,
      },
    },
  };

  const { getFieldDecorator } = props.form;

  return (
    <div>
      <Row>
        <Col xs={24} md={16}>
          <Form onSubmit={handleSubmit}>
            <Divider orientation="left">Cấu hình email</Divider>
            <Form.Item
              {...formItemLayout}
              label="Email nhận thông báo đặt phòng"
            >
              {getFieldDecorator('notifyToEmail', {
                initialValue: setting.notifyToEmail || '',
                rules: [{
                  type: 'email', message: 'The input is not valid E-mail!',
                }, {
                  required: true, message: 'Please input your E-mail!',
                }],
              })(
                <Input />
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label="Email đồng gửi về thông báo đặt phòng"
            >
              {getFieldDecorator('notifyCCEmail', {
                initialValue: setting.notifyCCEmail || '',
                rules: [{
                  type: 'email', message: 'The input is not valid E-mail!',
                }],
              })(
                <Input />
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label="Email đồng gửi về thông báo đặt phòng"
            >
              {getFieldDecorator('notifyBCCEmail', {
                initialValue: setting.notifyBCCEmail || '',
                rules: [{
                  type: 'email', message: 'The input is not valid E-mail!',
                }],
              })(
                <Input />
              )}
            </Form.Item>

            <Divider orientation="left">Cấu hình ngôn ngữ và ngày cuối tuần</Divider>
            <Form.Item
              {...formItemLayout}
              label="Ngôn ngữ hiển thị"
            >
              {getFieldDecorator('language', {
                initialValue: ['vi', 'en']
              })(
                <CheckboxGroup >
                  <Checkbox value="vi">Tiếng Việt</Checkbox>
                  <Checkbox value="en">English</Checkbox>
                </CheckboxGroup>
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label="Ngày cuối tuần"
            >
              {getFieldDecorator('weekend', {
                initialValue: ['1', '2']
              })(
                <CheckboxGroup >
                  <Checkbox value="1">Thứ hai</Checkbox>
                  <Checkbox value="2">Thứ ba</Checkbox>
                  <Checkbox value="3">Thứ tư</Checkbox>
                  <Checkbox value="4">Thứ năm</Checkbox>
                  <Checkbox value="5">Thứ sáu</Checkbox>
                  <Checkbox value="6">Thứ bảy</Checkbox>
                  <Checkbox value="7">Chủ nhật</Checkbox>
                </CheckboxGroup>
              )}
            </Form.Item>

            <Divider orientation="left">Đơn vị tiền tệ và mức thuế/ phí</Divider>
            <Form.Item
              {...formItemLayout}
              label="Đơn vị tiền tệ trong hệ thống"
            >
              {getFieldDecorator('usedCurrency', {
                initialValue: setting.usedCurrency || 'VND'
              })(
                <Select style={{ width: '100%' }}>
                  <Option value="VND">Việt Nam Đồng</Option>
                  <Option value="USD">Đô la</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label="Mức % thuế VAT được áp dụng"
            >
              {getFieldDecorator('valueAddedTaxPercent', {
                initialValue: setting.valueAddedTaxPercent || 10
              })(
                <InputNumber style={{ width: '100%' }} />
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label="Mức % thuế dịch vụ được áp dụng"
            >
              {getFieldDecorator('serviceTaxPercent', {
                initialValue: setting.serviceTaxPercent || 5
              })(
                <InputNumber style={{ width: '100%' }} />
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label="Giá đã bao gồm thuế"
            >
              {getFieldDecorator('includeTaxesInPrice', {
                initialValue: setting.includeTaxesInPrice || false
              })(
                <Checkbox />
              )}
            </Form.Item>

            <Divider orientation="left">Quy định về độ tuổi và mức phí trẻ em đi kèm</Divider>
            <Form.Item
              {...formItemLayout}
              label="Độ tuổi trẻ được miễn phí"
            >
              {getFieldDecorator('babyAgeDetermine', {
                initialValue: setting.babyAgeDetermine || '4'
              })(
                <Select style={{ width: '100%' }}>
                  <Option value="1">1</Option>
                  <Option value="2">2</Option>
                  <Option value="3">3</Option>
                  <Option value="4">4</Option>
                  <Option value="5">5</Option>
                  <Option value="6">6</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item
              {...formItemLayout}
              label="Độ tuổi trẻ tính phí"
            >
              {getFieldDecorator('childAgeDetermine', {
                initialValue: setting.childAgeDetermine || '8'
              })(
                <Select style={{ width: '100%' }}>
                  <Option value="6">6</Option>
                  <Option value="7">7</Option>
                  <Option value="8">8</Option>
                  <Option value="9">9</Option>
                  <Option value="10">10</Option>
                  <Option value="11">11</Option>
                  <Option value="12">12</Option>
                  <Option value="13">13</Option>
                  <Option value="14">14</Option>
                </Select>
              )}
            </Form.Item>

            <Form.Item {...tailFormItemLayout}>
              <Button type="primary" htmlType="submit" loading={loading}>Lưu</Button>
            </Form.Item>
          </Form>
        </Col>
      </Row>
    </div>
  )
}

const HotelSetting = Form.create()(HotelSettingForm);
export default HotelSetting;
